import React from 'react'; // eslint-disable-line
import styled from 'styled-components';

const Container = styled.div`
  display: flex;
  flex-basis: 90%;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin: 15px;
`;

export default Container;



