import React from 'react'; // eslint-disable-line
import styled from 'styled-components';

const Title = styled.h1`
  color: #1FA8EA;
  font-weight: 700;
  font-size: 1.5em;
  @media (min-width: 480px) {
    font-size: 2.5em;
  }
`;

export default Title;



