import React from 'react'; // eslint-disable-line
import styled from 'styled-components';

const Text = styled.p`
  color: #424242;
  font-weight: 100;
  font-size: .75em;
  @media (min-width: 480px) {
    font-size: 1.5em;
  }
`;

export default Text;



